<template>
  <div class="mine-collect">
    <div class="reture" @click="goBack">
      <img
        src="https://m.xiaomiyoupin.com/youpin/static/m/res/images/icons/icon_arrow_left_black.png"
      />
    </div>
    <div class="collect-tetle">我的收藏</div>
    <div class="collect-commodity" v-show="!empty">
      <van-card
        v-for="items in collects"
        :key="items.gid"
        :price="items.price_min / 100 + '.00'"
        :desc="items.attr_ext.custom_summary"
        :title="items.name"
        :thumb="items.pic_url"
        @click="goGoodDetailView(items)"
      />
    </div>
    <div class="empty" v-show="empty">
      <div>
        <img
          src="https://m.xiaomiyoupin.com/youpin/static/m/res/images/no_result/no_result_favor.png"
        />
      </div>
      <div class="now-empty">目前没有收藏商品哦～</div>
      <div class="stroll" @click="goHomePage">去首页逛逛</div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      collects: "",
      empty: false,
    };
  },
  created() {
    this.collects = JSON.parse(sessionStorage.getItem("collect"));
    if (JSON.parse(sessionStorage.getItem("collect")) == 0) {
      this.empty = true;
    }
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    // 进入详情
    goGoodDetailView(i) {
      this.$router.push({ name: "Particulars", params: { id: i.gid}});
      sessionStorage.setItem("particulars",JSON.stringify(i));
    },
    // 去首页
    goHomePage(){
        this.$router.push({path:'/'})
    }
  },
};
</script>

<style lang="scss" scoped>
.mine-collect {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: #f4f4f4;
  z-index: 23;

  .reture {
    position: absolute;
    left: 0;
    top: 0;

    img {
      width: 39px;
      height: 48px;
    }
  }

  .collect-tetle {
    height: 44px;
    line-height: 44px;
    color: rgb(51, 51, 51);
    font-size: 16px;
    text-align: center;
    background-color: white;
  }

  .collect-commodity {
    margin-top: 1px;
  }

  .empty {
    padding: 40px 0;
    background-color: #f4f4f4;

    img {
      width: 90px;
      height: 90px;
    }

    div {
      display: flex;
      justify-content: center;
    }
  }

  .now-empty {
    font-size: 12px;
    margin-top: 13px;
    color: #666;
  }

  .stroll {
    width: 95px;
    height: 35px;
    margin: 14px auto 0;
    padding: 8px 14px;
    border: 1px solid #8f8f8f;
    border-radius: 17px;
    color: #666;
    font-size: 13px;
  }
}
</style>